/*
 * Resetear todo el contenido del form Mis empleados
 */
function ResetForm()
{
    $('#txt_num_documento').val("");
    $('#txt_id_rh').val("");
    $('#txt_nombre').val("");
    $('#txt_apellidos').val("");
    $('#slt_genero').val("");
    $('#txt_fecha_ini').val("");
}

/*
 * Ocultar alert
 */

$(".alert").delay(6000).slideUp(200, function () {
    $(this).alert('close');
});

