/*
 * modal mis empleados
 */

var url = "/user";

//display modal form for task editing
$('.open-modal').click(function () {
    var cedula = $(this).val();
    $.get(url + '/' + cedula, function (data) {
        //success data
        console.log(data);
        /*Datos Personales*/
        $('#cedula').text(data[0].CEDULA);
        $('#title').text(data[0].NOMBRE_COMPLETO);
        $('#genero').text(data[0].GENERO);
        $('#edad').text(data[0].EDAD);
        $('#fecha_nto').text(data[0].FECHA_NACIMIENTO);
        $('#ciudad_nto').text(data[0].CIUDAD_NACIMIENTO);
        $('#estado_civil').text(data[0].ESTADO_CIVIL);
        $('#telefonos').text(data[0].TELEFONOS_FIJOS);
        $('#email').text(data[0].CORREOS_ELECTRONICOS);
        $('#direccion').text(data[0].DIRECCION_RESIDENCIA);
        $('#direccion').text(data[0].DIRECCION_RESIDENCIA);
        $('#estrato').text(data[0].ESTRATO_SOCIAL);
        $('#factor_rh').text(data[0].GRUPO_FACTOR_RH);
        /*Datos Contacto*/
        $('#contacto').text(data[0].CONTACTO);
        $('#tipo_dependencia').text(data[0].TIPO_DEPENDENCIA);
        $('#telefono_contacto').text(data[0].TELEFONO_CONTACTO);
        /*Datos Manpower*/
        $('#id_rh').text(data[0].ID_RH);
        $('#fecha_ini').text(data[0].FECHA_INICIO);
        $('#fecha_fin').text(data[0].FECHA_FIN);
        $('#tp_contrato').text(data[0].CONTRATO);
        $('#sucursal').text(data[0].SUCURSAL_MP);
        $('#puesto').text(data[0].PUESTO);
        $('#riesgo').text(data[0].CLASE_RIESGO);
        $('#eps').text(data[0].EPS);
        $('#afp').text(data[0].FONDO_PENSIONES);
        $('#fces').text(data[0].FONDO_CESANTIAS);
        $('#ccf').text(data[0].CAJA_COMPESACION_FAMILIAR);
        /*Datos Bancarios*/
        $('#tp_pago').text(data[0].TIPO_PAGO);
        $('#banco').text(data[0].BANCO);
        $('#nro_cuenta').text(data[0].N_CUENTA);
        $('#salario').text('$' + data[0].SALARIO);
        /*Abrir Modal*/
        $('#myModal').modal('show');
    })
});
